import { useState } from "react";
import { DropzoneArea } from "material-ui-dropzone";
import { Button, Container, Grid, Box } from "@material-ui/core";
import { AttachFile, PictureAsPdf } from "@material-ui/icons";
import moment from "moment";
import ReactExport from "react-export-excel";

import "./App.css";

const PDFJS = window.PDFJS;
PDFJS.workerSrc = "";

const ExcelFile = ReactExport.ExcelFile;
const ExcelSheet = ReactExport.ExcelFile.ExcelSheet;
const ExcelColumn = ReactExport.ExcelFile.ExcelColumn;

const BASE64_MARKER = ";base64,";
const KEYS_WORD = [
  {
    label: "Account number:",
    value: "accountNumber",
  },
  {
    label: "Your service address:",
    value: "yourServiceAddress",
  },
  {
    label: "Bill Issue date:",
    value: "billIssueDate",
  },
  {
    label: "Total amount due by",
    value: "totalAmountDue",
  },
];

const App = () => {
  const [dataExport, setDataExport] = useState(null);
  const [emptyData, setEmptyData] = useState(false);

  const getPageText = (pageNum, PDFDocumentInstance) => {
    return new Promise((resolve) => {
      PDFDocumentInstance.getPage(pageNum).then((pdfPage) => {
        pdfPage.getTextContent().then((textContent) => {
          resolve(textContent.items.map(({ str }) => str));
        });
      });
    });
  };

  const isValidData = (index, arrayString, value) => {
    if (index >= arrayString.length - 1) return "";
    const str = arrayString[index].trim();

    if (!str) return isValidData(index + 1, arrayString, value);

    if (value === "yourServiceAddress") {
      return str + " " + arrayString[index + 1].trim();
    }

    if (value === "totalAmountDue" && moment(str).isValid()) {
      return isValidData(index + 1, arrayString, value);
    }

    return str;
  };

  const pdfAsArray = (pdfAsArray) => {
    PDFJS.getDocument(pdfAsArray).then(
      (pdf) => {
        const pdfDocument = pdf;
        const pagesPromises = [];

        for (let i = 0; i < pdf.pdfInfo.numPages; i++) {
          ((pageNumber) => {
            pagesPromises.push(getPageText(pageNumber, pdfDocument));
          })(i + 1);
        }

        Promise.all(pagesPromises).then((pagesText) => {
          let indexOfKeys = [];
          for (let pageNum = 0; pageNum < pagesText.length; pageNum++) {
            const arrayString = pagesText[pageNum];

            for (let iKey = 0; iKey < KEYS_WORD.length; iKey++) {
              const { label } = KEYS_WORD[iKey];
              const index = arrayString.findIndex((t) => t.startsWith(label));
              if (index !== -1) {
                indexOfKeys.push(index);
              }
            }

            if (indexOfKeys.length === KEYS_WORD.length) {
              let result = {
                accountNumber: "",
                yourServiceAddress: "",
                billIssueDate: "",
                totalAmountDue: "",
              };
              for (let iKey = 0; iKey < KEYS_WORD.length; iKey++) {
                const { value } = KEYS_WORD[iKey];
                const index = indexOfKeys[iKey] + 1;
                const str = isValidData(index, arrayString, value);
                result[value] = str;
              }
              setDataExport(result);
            } else if (pageNum === pagesText.length - 1) {
              setEmptyData(true);
            } else {
              indexOfKeys = [];
            }
          }
        });
      },
      () => setEmptyData(true)
    );
  };

  const convertDataURIToBinary = (dataURI) => {
    const base64Index = dataURI.indexOf(BASE64_MARKER) + BASE64_MARKER.length;
    const base64 = dataURI.substring(base64Index);
    const raw = window.atob(base64);
    const rawLength = raw.length;
    let array = new Uint8Array(new ArrayBuffer(rawLength));

    for (let i = 0; i < rawLength; i++) {
      array[i] = raw.charCodeAt(i);
    }
    pdfAsArray(array);
  };

  const onExtractText = (files) => {
    setEmptyData(false);
    setDataExport(null);
    if (files.length > 0) {
      let fReader = new FileReader();
      fReader.readAsDataURL(files[0]);
      fReader.onloadend = function (event) {
        convertDataURIToBinary(event.target.result);
      };
    }
  };

  const handlePreviewIcon = (fileObject, classes) => {
    const { type } = fileObject.file;
    const iconProps = {
      className: classes.image,
    };

    switch (type) {
      case "application/pdf":
        return <PictureAsPdf {...iconProps} />;
      default:
        return <AttachFile {...iconProps} />;
    }
  };

  return (
    <Container maxWidth="sm">
      <Grid
        container
        spacing={3}
        direction="column"
        alignItems="center"
        justifyContent="center"
        style={{ minHeight: "100vh" }}
      >
        <Grid item style={{ width: "100%" }}>
          <DropzoneArea
            dropzoneText="Drag and drop a file or click to upload"
            filesLimit={1}
            getPreviewIcon={handlePreviewIcon}
            showAlerts={false}
            acceptedFiles={["application/pdf"]}
            previewGridClasses={{ container: "justify-content-center", item: "m-4" }}
            onChange={onExtractText}
          />
        </Grid>
        <Grid item style={{ width: "100%" }}>
          {emptyData && <Box style={{ textAlign: "center" }}>Not found</Box>}
          <ExcelFile
            filename={moment().toISOString()}
            element={
              <Box align="center">
                {dataExport && (
                  <Button variant="contained" color="primary">
                    Download Excel
                  </Button>
                )}
              </Box>
            }
          >
            <ExcelSheet data={[dataExport]} name="Employees">
              <ExcelColumn label="Account Number" value="accountNumber" />
              <ExcelColumn label="Your Service Address" value="yourServiceAddress" />
              <ExcelColumn label="Bill Issue Date" value="billIssueDate" />
              <ExcelColumn label="Total Amount Due" value="totalAmountDue" />
            </ExcelSheet>
          </ExcelFile>
        </Grid>
      </Grid>
    </Container>
  );
};

export default App;
